<template>
  <div class="id_card_upload">
    <div class="pay_way_title">上传评价图片</div>
    <van-row class="upload_hint text-desc">
      <van-icon name="hint"/>
      温馨提示: 请参考下方示例图
    </van-row>
    <van-row gutter="20" class="id_card_row upload_box">
      <van-col span=24>
        <div class="upload_wrap">
          <van-uploader @oversize="maxReadBack" max-size="4194304" :after-read="onRead1">
            <div class="add_btn">
              <div v-if="comment_pic">
                <img :src="comment_pic">
              </div>
              <div v-else>
                <van-icon name="photograph" />
                <p>上传照片</p>
              </div>
            </div>
          </van-uploader>
        </div>
      </van-col>
    </van-row>
    <van-row gutter="10" class="id_card_row">
      <van-col span=24>
        <div class="text-desc red_color">截图示列（点击可放大）</div>
        <img :src="demoImg" @click="showImagePreview(demoImg)" width="50%">
      </van-col>
    </van-row>
  </div>
</template>

<script>
import { Row, Col, Uploader, ImagePreview } from "vant";
// Vue.use(Uploader);
export default {
  name: "commentUpload",
  props:['comment_pic'],
  data() {
    return {
      files: {
        comment_file:''
      },
      comment_pic: '',
      demoImg: require(`@/assets/images/hp.jpg`)
    };
  },
  methods: {
    onRead1(files){
      let img = new Image();
      img.src = files.content;
      let that=this;
      img.onload=function(){
        let f1 = that.compress(img);
        that.files.comment_file = f1;
        that.comment_pic = f1.content
      }
      this.$emit('setFiles',this.files);
    },
    // 压缩图片
    compress(img){
        let originWidth = img.width, // 压缩后的宽
        originHeight=img.height,
        maxWidth = 600, maxHeight = 400,
        quality = 0.8, // 压缩质量
        canvas = document.createElement('canvas'),
        drawer = canvas.getContext("2d");
        canvas.width = maxWidth;
        canvas.height = originHeight/originWidth*maxWidth;
        drawer.drawImage(img, 0, 0, canvas.width, canvas.height);
        let base64 = canvas.toDataURL("image/jpeg", quality); // 压缩后的base64图片
        let file = this.dataURLtoFile(base64,Date.parse(Date())+'.jpg');
        file={content:base64,file:file};
        //console.log('压缩后的file');
        //console.log(file);//压缩后的file
        return file;
    },
     // base64转file
    dataURLtoFile(dataurl,filename){
      let arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
      bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
      while(n--){
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr],filename,{type:mime});
    },
    // 图片超出限制后 进行压缩
    maxReadBack(files){
      Toast('图片不能超出4M')
    },
    showImagePreview(img) {
       ImagePreview([img]);
    }
  },
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [ImagePreview.name]: ImagePreview,
    [Uploader.name] : Uploader
  }
};
</script>

<style lang="scss" scoped>
.pay_way_title {
  font-weight: bold;
  padding: 0;
}
.id_card_upload {
  margin: 10px 0 10px 0;
  background-color: #fff;
  padding: 15px;
  > div {
    margin-bottom: 15px;
  }
}
.upload_hint {
  position: relative;
  padding-left: 20px;
  line-height: 1.6;
  i {
    position: absolute;
    top: 2px;
    left: 0;
  }
}
.id_card_row {
  > div {
    text-align: center;
  }
  .text-desc {
    margin-bottom: 8px;
  }
  .red_color {
    color: darkred;
    font-size: 14px;
    font-weight: 700;
  }
}
.upload_box .upload_wrap {
  position: relative;
  border: 1px dashed $gray;
  min-height: 145px;
  box-sizing: border-box;
  padding: 5px;
  text-align: center;
  img {
    max-width: 100%;
    height: 120px;
  }
  .van-uploader {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
  .add_btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: $gray;
    p {
      white-space: nowrap;
      margin: 0;
      font-size: 18px;
    }
    i {
      font-size: 26px;
    }
  }
}
</style>



